
.container {
    justify-content: center;
    align-items: center;
    height: 100vh;
    max-width: min(90vw, 1200px);
    width: 70rem;
    margin: auto;
  }
  
  .card1{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .card2{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .left {
    flex: 1;
    padding: 20px;
  }
  
  .right {
    flex: 1;
    padding: 50px;
    height: 300px;
    background-color: #f5f5f5;
  }
  
  .right form {
    max-width: 300px;
    margin: auto;
  }
  
  input[type=text], input[type=password] {
    width: 100%;
    margin-bottom: 10px;
    padding: 8px;
    box-sizing: border-box;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .remember-me{
    display: flex;
    height: 10%;
  }

  .remember-me p {
    margin-left: 5px;
  }